    }
    50% {
        opacity: 0;
        -moz-transform: scale(1.20);
    }
    100% {
        opacity: 0;
        -moz-transform: scale(1.20);
    }
}

@-o-keyframes imageAnimation {
    0% {
        opacity: 0;
        -o-transform: scale(1.05);
        -o-animation-timing-function: ease-in;
    }
    10% {
        opacity: 1;
        -o-transform: scale(1.10);
    }
    20% {
        opacity: 1;
