    }
    30% {
        opacity: 0;
        -o-transform: scale(1.20);
    }
    50% {
        opacity: 0;
        -o-transform: scale(1.20);
    }
    100% {
        opacity: 0;
        -o-transform: scale(1.20);
    }
}

@-ms-keyframes imageAnimation {
    0% {
        opacity: 0;
        -ms-transform: scale(1.05);
        -ms-animation-timing-function: ease-in;
    }
    10% {
        opacity: 1;
        -ms-transform: scale(1.10);
    }
    20% {
        opacity: 1;
        -ms-transform: scale(1.15);
    }
    30% {
        opacity: 0;
