    }
    50% {
        opacity: 0;
        -ms-transform: scale(1.20);
    }
    100% {
        opacity: 0;
        -ms-transform: scale(1.20);
    }
}

@keyframes imageAnimation {
    0% {
        opacity: 0;
        transform: scale(1.05);
        animation-timing-function: ease-in;
    }
    10% {
        opacity: 1;
        transform: scale(1.10);
    }
    20% {
        opacity: 1;
