    }
    100% {
        opacity: 0;
        -webkit-transform: scale(1.20);
    }
}

/* Show at least something when animations not supported */
.no-cssanimations .cb-slideshow li img {
    opacity: 1;
}

//Custom
ul.cb-slideshow.cell.cell--12-col {
    margin: 0;
    padding: 0;
}

.grid {
    margin: -8px;
}

.t_center {
    text-align: center;
}

.t_left {
    text-align: left;
}

.t_right {
    text-align: right;
}

.daterangepicker.dropdown-menu.show-calendar.opensright {
    display: none;
}

// Custom calendar
.mdtp {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 2000;
}

.mdtp > .mdtp-main {
    background: #fff;
    max-width: 300px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    position: relative;
    left: 50%;
    top: 158px;
    margin-left: -150px;
}

.mdtp > .mdtp-main  header.mdtp-header {
    background: #007d72;
    color: #fff;
    text-align: center;
    padding: 0.3rem;
}

.mdtp > .mdtp-main  div.mdtp-date {
    background: #009688;
    text-align: center;
    color: #fff;
    padding: 10px;
}

.mdtp > .mdtp-main  div.mdtp-date > div {
    padding: 0;
    margin: 0;
}

.mdtp table.mdtp-picker-days tr > td > a.selected,
a.ui-state-default.ui-state-active {
    background: #009688;
    color: #fff;
    font-weight: 500;
}

.mdtp .center {
    text-align: center;
}
.mdtp .left {
    float: left;
}
.mdtp .right {
    float: right;
}
.mdtp .p10 {
    width: 10%;
    display: inline-block;
}
.mdtp div.mdtp-actual-month {
    font-size: 1.5em;
}
.mdtp .p80 {
    width: 80%;
    display: inline-block;
}
.mdtp div.mdtp-actual-num {
    font-size: 3em;
    line-height: 0.9;
}
.mdtp div.mdtp-actual-year {
    font-size: 1.5em;
    color: #00BBAB;
}
.mdtp .mdtp-close {
    position: absolute;
    top: 0.5em;
    right: 1rem;
}
.mdtp .mdtp-close > a > i {
    font-size: 1em;
}


