}

.ui-datepicker table {
    border: none;
    margin: 0;
    width: 100%;
}

.ui-datepicker {
    width: 100%;
    padding: 0;
}

.ui-datepicker td span, .ui-datepicker td a {
    color: #212121;
    font-weight: 600;
    text-decoration: none;
    padding: 0.5rem 0.5rem;
    border-radius: 50% !important;
    text-align: center;
    display: inline-block;
    width: 15px;
    height: 15px;
    line-height: 15px;
}

.ui-datepicker th {
    color: #757575;
    text-align: center;
    font-weight: 700;
    padding: 0.4rem 0.3rem;
}

.ui-datepicker td {
    // font-weight: 700;
    font-size: 0.9rem;
    text-align: center;
    padding: 1rem 0.3rem;
}

.ui-state-highlight {
    background: #ddd;
}

.ui-state-hover {
    margin: 0;
    padding: 0;
}

.ui-icon {
    text-indent: inherit;
    overflow: visible;
}

.ui-datepicker .ui-datepicker-next {
    right: 10px;
}

.ui-datepicker .ui-datepicker-prev {
    left: 10px;
}
